import React, { Component } from 'react';
import { connect } from 'react-redux'
import { updateTabNavigation } from '../store/actions'
import request from 'superagent';
import { confirmAlert } from 'react-confirm-alert'; // Import
import 'react-confirm-alert/src/react-confirm-alert.css' // Import css
import Select from 'react-select';
import LoadingOverlay from 'react-loading-overlay';
import { AllModules } from 'ag-grid-react';
import { AgGridReact } from 'ag-grid-react';
import Popup from "reactjs-popup";
import 'ag-grid-community/dist/styles/ag-grid.css';
import 'ag-grid-community/dist/styles/ag-theme-alpine.css';
import { Editor } from 'react-draft-wysiwyg';
import 'react-draft-wysiwyg/dist/react-draft-wysiwyg.css';
import { convertToRaw } from 'draft-js';
import draftToMarkdown from 'draftjs-to-markdown';
import CreatableSelect from 'react-select/creatable';

class Users extends Component {
    constructor(props) {
        super(props)
        this.state = {
            isLoading: false,
            roleNameList: [],
            selectedRoleCard: null,
            selectedSequence: null,
            modules: AllModules,
            columnDefs: [
                {
                    field: 'id',
                    headerName: 'id',
                    maxWidth: 150,
                },
                {
                    field: 'username',
                    headerName: 'Username',
                    maxWidth: 150,
                },
                {
                    field: 'usersFirstName',
                    headerName: 'FirstName',
                    maxWidth: 150
                },
                {
                    field: 'usersLastName',
                    headerName: 'LastName',
                    maxWidth: 150,
                },
                {
                    field: 'role',
                    headerName: 'Role',
                    cellStyle: { 'white-space': 'normal' },
                    maxWidth: 150
                },
                {
                    field: 'email',
                    headerName: 'Email Address',
                    cellStyle: { 'white-space': 'normal' }
                }
            ],
            defaultColDef: {
                flex: 1,
                minWidth: 100,
                sortable: true,
                resizable: true,
                autoHeight: true
            },
            getRowHeight: function (params) {
                return 18 * (Math.floor(params.data.description.length / 50) + 1);
            },
            rowSelection: 'single',
            selectedRow: null,
            paginationPageSize: 10,
            rowData: [],
            editorState: null,
            organisation: '',
            userRoleList: [],
            selectedUserRole: null,
            recentlyCreatedOrgUserId: '',
            isPopupOpen: false,
            actionType: null,
            username: "",
            password: "",
            confirm_password: "",
            usersFirstName: "",
            usersLastName: "",
            email: "",
            role: "",
            age: "",
            phone: "",
            website: "",
            addressId: "",
            salutation: "",
            selectedSalutation: null,
            SalutationList: [{ label: 'Mr.', value: 'Mr.' }, { label: 'Mrs.', value: 'Mrs.' }]
        }
        this.onCreateUser = this.onCreateUser.bind(this)
        this.onAddEditUser = this.onAddEditUser.bind(this)
        this.onDeleteUser = this.onDeleteUser.bind(this)
        this.onEditUserName = this.onEditUserName.bind(this)

        process.env.REACT_APP_STAGE == 'production' ? this.apiBase = process.env.REACT_APP_API_URL + '/api' : this.apiBase = `${window.location.protocol}//${window.location.hostname}:${process.env.REACT_APP_API_PORT}`
    }

    onGridReady = params => {
        this.gridApi = params.api;
        this.gridColumnApi = params.columnApi;
    }

    componentDidMount() {
        this.loadUsers()
    }

    onBtStartEditing = (key, char, pinned) => {
        this.gridApi.setFocusedCell(0, 'lastName', pinned);
        this.gridApi.startEditingCell({
            rowIndex: 0,
            colKey: 'lastName',
            rowPinned: pinned,
            keyPress: key,
            charPress: char,
        });
    }

    async loadUsers() {
        this.setState({
            isLoading: true
        })
        try {
            const r = await request
                .get(process.env.REACT_APP_API_URL + 'users')
                .set('authorization', `Bearer ${localStorage.getItem('admin_access_token')}`);
            const response = r.body
            if (response.success) {
                this.setState({
                    roleNameList: response.ResponsePacket,
                    rowData: response.ResponsePacket,
                    isLoading: false
                }, () => {
                    console.log(this.state.roleNameList)
                })
            } else {
                this.setState({
                    isLoading: false
                })
            }
        } catch (catchError) {
            this.setState({
                isLoading: false
            })
            alert("Error...")
        }

    }

    async onCreateUser() {
        const { selectedUserRole, selectedSequence,
            username,
            password,
            confirm_password,
            usersFirstName,
            usersLastName,
            email,
            role,
            age,
            phone,
            website,
            addressId,
            salutation,
            selectedSalutation
        } = this.state
        this.setState({
            isLoading: true
        })

        const r = await request
            .post(process.env.REACT_APP_API_URL + 'users')
            .send({
                username: usersFirstName + Math.floor(Math.random() * 23434),
                password: password,
                confirm_password: password,
                usersFirstName: usersFirstName,
                usersLastName: usersLastName,
                email: email,
                role: role,
                age: 20,
                phone: phone,
                website: website,
                addressId: addressId,
                salutation: selectedSalutation.value,
                isDeleted: false
            })
        const response = r.body

        if (response.success) {
            this.setState({
                name: "",
                organisationId: "",
                description: "",
                isLoading: false,
                isPopupOpen: false
            }, () => {
                this.loadUsers()
                confirmAlert({
                    title: 'Users',
                    message: response.message,
                    buttons: [
                        {
                            label: 'Ok',
                            onClick: () => {

                            }
                        }
                    ],
                    closeOnClickOutside: false,
                    closeOnEscape: false
                })
            })
        } else {
            this.setState({
                isLoading: false
            }, () => {
                confirmAlert({
                    title: 'Users',
                    message: response.message,
                    buttons: [
                        {
                            label: 'Ok',
                            onClick: () => {

                            }
                        }
                    ],
                    closeOnClickOutside: false,
                    closeOnEscape: false
                })
            })
        }
    }

    async onEditUserName() {
        const {
            selectedUserRole,
            selectedSequence,
            selectedRow,
            id,
            usersFirstName,
            usersLastName,
            email,
            phone,
            website,
            salutation,
            addressId,
            password,
            username,
            role,
            confirm_password,
            age,
            selectedSalutation
        } = this.state
        this.setState({
            isLoading: true
        })

        const r = await request
            .patch(process.env.REACT_APP_API_URL + 'users/id/' + selectedRow.id)
            .set('authorization', `Bearer ${localStorage.getItem('admin_access_token')}`)
            .send({
                password: 'test@123',
                confirm_password: 'test@123',
                usersFirstName,
                usersLastName,
                email,
                role,
                age,
                phone,
                website,
                addressId,
                salutation: selectedSalutation.value,
            })
        const response = r.body
        if (response.success) {
            this.setState({
                isLoading: false,
                isPopupOpen: false,
                selectedRow: null
            }, () => {
                this.loadUsers()
                confirmAlert({
                    title: 'User',
                    message: response.message,
                    buttons: [
                        {
                            label: 'Ok',
                            onClick: () => {

                            }
                        }
                    ],
                    closeOnClickOutside: false,
                    closeOnEscape: false
                })
            })
        } else {
            this.setState({
                isLoading: false
            }, () => {
                confirmAlert({
                    title: 'User',
                    message: response.message,
                    buttons: [
                        {
                            label: 'Ok',
                            onClick: () => {

                            }
                        }
                    ],
                    closeOnClickOutside: false,
                    closeOnEscape: false
                })
            })
        }
    }

    clearState = () => {
        this.setState({
            selectedRow: null,
            isPopupOpen: false,
            organisationId: '',
            isDeleted: false,
        })
    }

    async onAddEditUser(type) {
        const { selectedRow } = this.state
        if (!selectedRow && type == 'edit') {
            return
        } else {

            if (type == 'edit') {
                this.setState({
                    isPopupOpen: true,
                    actionType: 'Update User',
                    id: selectedRow.id,
                    usersFirstName: selectedRow.usersFirstName,
                    usersLastName: selectedRow.usersLastName,
                    email: selectedRow.email,
                    phone: selectedRow.phone,
                    website: selectedRow.website,
                    selectedSalutation: { label: selectedRow.salutation, value: selectedRow.salutation },
                    addressId: selectedRow.addressId,
                    username: selectedRow.username,
                    role: selectedRow.role,
                    age: selectedRow.age
                })
            } else {
                this.setState({
                    isPopupOpen: true,
                    id: '',
                    selectedUserRole: null,
                    selectedSequence: null,
                    isDeleted: false,
                    actionType: 'Add User',
                    usersFirstName: "",
                    usersLastName: "",
                    email: "",
                    phone: "",
                    website: "",
                    selectedSalutation: null,
                    addressId: "",
                    password: "",
                    username: "",
                    role: "Admin",
                    confirm_password: "",
                    age: 21
                })
            }
        }
    }

    async onDeleteUser() {
        const { selectedRow } = this.state
        confirmAlert({
            title: 'Delete RoleName',
            message: "Are you sure you wants to delete the record : " + selectedRow.usersFirstName + "?",
            buttons: [
                {
                    label: 'Confirm',
                    onClick: () => {
                        this.deleteUser()
                    }
                },
                {
                    label: 'Cancel',
                    onClick: () => {

                    }
                }
            ],
            closeOnCllickOutside: false,
            closeOnEscape: false
        })
    }

    async deleteUser() {
        const { selectedRow } = this.state
        this.setState({ isLoading: true })
        const r = await request
            .del(process.env.REACT_APP_API_URL + 'users/id/' + selectedRow.id)
            .set('authorization', `Bearer ${localStorage.getItem('admin_access_token')}`);
        const response = r.body
        if (response.success) {
            this.setState({
                name: "",
                organisationId: "",
                description: "",
                isLoading: false,
                selectedRow: null
            }, () => {
                this.loadUsers()
                confirmAlert({
                    title: 'Users',
                    message: response.message,
                    buttons: [
                        {
                            label: 'Ok',
                            onClick: () => {

                            }
                        }
                    ],
                    closeOnClickOutside: false,
                    closeOnEscape: false
                })
            })
        } else {
            this.setState({
                isLoading: false
            }, () => {
                confirmAlert({
                    title: 'Users',
                    message: response.message,
                    buttons: [
                        {
                            label: 'Ok',
                            onClick: () => {

                            }
                        }
                    ],
                    closeOnClickOutside: false,
                    closeOnEscape: false
                })
            })
        }
    }

    onTextChange = (e) => {
        this.setState({
            [e.target.id]: e.target.value
        })
    }

    handleImageUploadChange(event) {
        this.setState({
            gameBoard: URL.createObjectURL(event.target.files[0])
        })
    }

    onClickTab = (tabName) => {
        this.props.updateTabNavigation({ TabName: tabName })
    }

    handleChange = selectedRoleCard => {
        this.setState(
            { selectedRoleCard },
            () => console.log(`Option selected:`, this.state.selectedRoleCard)
        );
    }

    handleSequenceNumber = selectedSequence => {
        this.setState(
            { selectedSequence },
            () => console.log(`Option selected:`, this.state.selectedSequence)
        );
    }

    onEditorStateChange = (editorState) => {
        this.setState({
            editorState,
            description: editorState && draftToMarkdown(convertToRaw(editorState.getCurrentContent())),
        })
    }

    onUserRoleChange = (newValue, actionMeta) => {
        if (actionMeta.action == "select-option") {
            this.setState({
                selectedUserRole: newValue,
                organisation: newValue.label,
            })
        }
    }

    onSalutationChange = (newValue, actionMeta) => {
        if (actionMeta.action == "select-option") {
            this.setState({
                selectedSalutation: newValue,
            })
        }
    }

    onSelectionChanged = () => {
        var selectedRows = this.gridApi.getSelectedRows();
        this.setState({
            selectedRow: selectedRows && selectedRows.length > 0 ? selectedRows[0] : null
        })
    }

    render() {
        const { roleNameList, isLoading } = this.state
        return (
            <LoadingOverlay
                active={isLoading}
                spinner
            >
                <div className="card m-10">
                    <section className="content">
                        <div className="row">
                            <div className="col-md-12">
                                <div className="card-body">
                                    <div style={{ padding: '0 15px 15px', display: 'inline-block', width: '100%' }}>
                                        <button
                                            disabled={!this.state.selectedRow}
                                            type="button"
                                            className="btn btn-danger float-right btn-sm"
                                            onClick={this.onDeleteUser}
                                        >{'Delete Record'}</button>
                                        <button
                                            disabled={!this.state.selectedRow}
                                            type="button"
                                            className="btn btn-success float-right btn-sm mx-2"
                                            onClick={this.onAddEditUser.bind(this, 'edit')}
                                        >{'Update Record'}</button>
                                        <button
                                            type="button"
                                            className="btn btn-success float-right btn-sm"
                                            onClick={this.onAddEditUser.bind(this, 'add')}
                                        >{'Add New'}</button>
                                    </div>
                                    <div className="card-body table-responsive p-0">
                                        <div style={{ width: '100%', height: '550px' }}>
                                            <div
                                                id="RoleNameGrid"
                                                style={{
                                                    height: '550px',
                                                    width: '100%',
                                                }}
                                                className="ag-theme-alpine"
                                            >
                                                <AgGridReact
                                                    modules={this.state.modules}
                                                    columnDefs={this.state.columnDefs}
                                                    defaultColDef={this.state.defaultColDef}
                                                    enableCellTextSelection={true}
                                                    rowSelection={this.state.rowSelection}
                                                    onGridReady={this.onGridReady}
                                                    rowData={this.state.rowData}
                                                    pagination={true}
                                                    paginationPageSize={this.state.paginationPageSize}
                                                    onSelectionChanged={this.onSelectionChanged.bind(this)}
                                                    rowDeselection={true}
                                                />
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </section>
                </div>
                <Popup open={this.state.isPopupOpen} closeOnDocumentClick={false} repositionOnResize={true}>
                    <section className="content">
                        <div className="row">
                            <div className="col-md-12">
                                <div className="form-group align-center">
                                    <h3 className="text-center m-2">{this.state.actionType} </h3>
                                </div>
                                <div style={{ marginTop: '40px' }}>
                                    <div className="card-body">
                                        <div className="form-group">
                                            <label htmlFor="Salutation">Salutation</label>
                                            <CreatableSelect
                                                isClearable
                                                value={this.state.selectedSalutation}
                                                onChange={this.onSalutationChange}
                                                onInputChange={this.handleInputChange}
                                                options={this.state.SalutationList}
                                            />
                                        </div>
                                        <div className="form-group">
                                            <label htmlFor="UserRole">User Role</label>
                                            <CreatableSelect
                                                isClearable
                                                value={this.state.selectedUserRole}
                                                onChange={this.onUserRoleChange}
                                                onInputChange={this.handleInputChange}
                                                options={this.state.userRoleList}
                                            />
                                        </div>
                                        <div className="form-group">
                                            <label htmlFor="usersFirstName">First Name </label>
                                            <input type="text" onChange={this.onTextChange} id="usersFirstName" value={this.state.usersFirstName} className="form-control" />
                                        </div>
                                        <div className="form-group">
                                            <label htmlFor="usersLastName">Last Name </label>
                                            <input type="text" onChange={this.onTextChange} id="usersLastName" value={this.state.usersLastName} className="form-control" />
                                        </div>
                                        <div className="form-group">
                                            <label htmlFor="email">Email Address </label>
                                            <input type="text" onChange={this.onTextChange} id="email" value={this.state.email} className="form-control" />
                                        </div>
                                        {
                                            this.state.actionType == 'Add User' &&
                                            <div className="form-group">
                                                <label htmlFor="password">Password </label>
                                                <input type="password" onChange={this.onTextChange} id="password" value={this.state.password} className="form-control" />
                                            </div>
                                        }
                                        <div className="form-group">
                                            <label htmlFor="phone">Phone </label>
                                            <input type="text" onChange={this.onTextChange} id="phone" value={this.state.phone} className="form-control" />
                                        </div>
                                    </div>
                                </div>
                                <div style={{ padding: '0 15px 15px', display: 'inline-block', width: '100%' }}>
                                    <button
                                        type="button"
                                        className="btn btn-warning float-right btn-sm mx-1"
                                        onClick={() => {
                                            this.setState({ isPopupOpen: false }, () => {
                                                this.clearState()
                                            })
                                        }}
                                    >{'Cancel'}</button>
                                    <button
                                        type="button"
                                        className="btn btn-success float-right btn-sm mx-1"
                                        onClick={this.state.actionType == 'Update User' ? this.onEditUserName : this.onCreateUser}
                                    >{'Submit'}</button>
                                </div>
                            </div>
                        </div>
                    </section>
                </Popup>
            </LoadingOverlay>
        )
    }
}

const mapStateToProps = state => ({
    tabSettings: state.userReducer.tabSettings,
    roleNameList: state.userReducer.playerList,

})

const mapDispatchToProps = dispatch => ({
    updateTabNavigation: (data) => { dispatch(updateTabNavigation(data)) },
})

export default connect(
    mapStateToProps,
    mapDispatchToProps
)(Users)
