import React, { Component } from 'react';
import { connect } from 'react-redux'
import { updateTabNavigation } from '../store/actions'
import request from 'superagent';
import { confirmAlert } from 'react-confirm-alert'; // Import
import 'react-confirm-alert/src/react-confirm-alert.css' // Import css
import Select from 'react-select';
import LoadingOverlay from 'react-loading-overlay';
import { AllModules } from 'ag-grid-react';
import { AgGridReact } from 'ag-grid-react';
import Popup from "reactjs-popup";
import 'ag-grid-community/dist/styles/ag-grid.css';
import 'ag-grid-community/dist/styles/ag-theme-alpine.css';
import { Editor } from 'react-draft-wysiwyg';
import 'react-draft-wysiwyg/dist/react-draft-wysiwyg.css';
import { convertToRaw } from 'draft-js';
import draftToMarkdown from 'draftjs-to-markdown';
import CreatableSelect from 'react-select/creatable';

class CategoriesType extends Component {
    constructor(props) {
        super(props)
        this.state = {
            isLoading: false,
            selectedSequence: null,
            modules: AllModules,
            columnDefs: [
                {
                    field: 'id',
                    headerName: 'Type Id',
                },
                {
                    field: 'typeValue',
                    headerName: 'Type Value',
                },
                {
                    field: 'typeDescription',
                    headerName: 'Description',
                },
                {
                    field: 'typeStatus',
                    headerName: 'CategoriesType Status',
                    valueGetter: function (param) {
                        return param.data.typeStatus == 0 ? 'InActive' : 'Active'
                    },
                },
                {
                    field: 'typeOrder',
                    headerName: 'Type Order',
                }               
            ],
            defaultColDef: {
                flex: 1,
                minWidth: 100,
                sortable: true,
                resizable: true,
                autoHeight: true
            },
            getRowHeight: function (params) {
                return 18 * (Math.floor(params.data.description.length / 50) + 1);
            },
            rowSelection: 'single',
            selectedRow: null,
            paginationPageSize: 10,
            rowData: [],
            editorCategoriesType: null,
            userRoleList: [],
            selectedUserRole: null,
            recentlyCreatedOrgUserId: '',
            isPopupOpen: false,
            actionType: null,

            typeName: '',
            typeValue:'',
            typeDescription:'',
            typeOrder:'',
            typeDefault:'',
            typeStatus:'',
            countryId: null,
            selectedCountry: null,
            countryList: []

        }
        this.onCreateCategoriesType = this.onCreateCategoriesType.bind(this)
        this.onAddEditCategoriesType = this.onAddEditCategoriesType.bind(this)
        this.onDeleteCategoriesType = this.onDeleteCategoriesType.bind(this)
        this.onEditCategoriesType = this.onEditCategoriesType.bind(this)

        process.env.REACT_APP_STAGE == 'production' ? this.apiBase = process.env.REACT_APP_API_URL + '/api' : this.apiBase = `${window.location.protocol}//${window.location.hostname}:${process.env.REACT_APP_API_PORT}`
    }

    onGridReady = params => {
        this.gridApi = params.api;
        this.gridColumnApi = params.columnApi;
    }

    componentDidMount() {
        this.loadCategoriesType()
    }

    onBtStartEditing = (key, char, pinned) => {
        this.gridApi.setFocusedCell(0, 'lastName', pinned);
        this.gridApi.startEditingCell({
            rowIndex: 0,
            colKey: 'lastName',
            rowPinned: pinned,
            keyPress: key,
            charPress: char,
        });
    }

    async loadCategoriesType() {
        this.setState({
            isLoading: true
        })
        try {
            const r = await request
                .get(process.env.REACT_APP_API_URL + 'categoriestype')
                .set('authorization', `Bearer ${localStorage.getItem('admin_access_token')}`);
            const response = r.body
            if (response.success) {
                this.setState({
                    rowData: response.ResponsePacket,
                    isLoading: false
                })
            } else {
                this.setState({
                    isLoading: false
                })
            }
        } catch (catchError) {
            this.setState({
                isLoading: false
            })
            alert("Error...")
        }
    }

    async onCreateCategoriesType() {
        const {
            typeName,
            typeValue,
            typeDescription,
            typeOrder,
            typeDefault,
            typeStatus

        } = this.state
        this.setState({
            isLoading: true
        })

        const r = await request
            .post(process.env.REACT_APP_API_URL + 'categoriestype')
            .send({
                typeName,
                typeValue,
                typeDescription,
                typeOrder,
                typeDefault,
                typeStatus
            })
        const response = r.body

        if (response.success) {
            this.setState({
                typeName: '',
                typeValue:'',
                typeDescription:'',
                typeOrder:'',
                typeDefault:'',
                typeStatus:'',
                isLoading: false,
                isPopupOpen: false
            }, () => {
                this.loadCategoriesType()
                confirmAlert({
                    title: 'CategoriesType',
                    message: response.message,
                    buttons: [
                        {
                            label: 'Ok',
                            onClick: () => {

                            }
                        }
                    ],
                    closeOnClickOutside: false,
                    closeOnEscape: false
                })
            })
        } else {
            this.setState({
                isLoading: false
            }, () => {
                confirmAlert({
                    title: 'CategoriesType',
                    message: response.message,
                    buttons: [
                        {
                            label: 'Ok',
                            onClick: () => {

                            }
                        }
                    ],
                    closeOnClickOutside: false,
                    closeOnEscape: false
                })
            })
        }
    }

    async onEditCategoriesType() {
        const {
            selectedRow,
            id,
            typeName,
            typeValue,
            typeDescription,
            typeOrder,
            typeDefault,
            typeStatus
        } = this.state
        this.setState({
            isLoading: true
        })

        const r = await request
            .patch(process.env.REACT_APP_API_URL + 'categoriestype/id/' + selectedRow.id)
            .set('authorization', `Bearer ${localStorage.getItem('admin_access_token')}`)
            .send({
                id,
                typeName,
                typeValue,
                typeDescription,
                typeOrder,
                typeDefault,
                typeStatus
            })
        const response = r.body
        if (response.success) {
            this.setState({
                isLoading: false,
                isPopupOpen: false,
                selectedRow: null
            }, () => {
                this.loadCategoriesType()
                confirmAlert({
                    title: 'CategoriesType',
                    message: response.message,
                    buttons: [
                        {
                            label: 'Ok',
                            onClick: () => {

                            }
                        }
                    ],
                    closeOnClickOutside: false,
                    closeOnEscape: false
                })
            })
        } else {
            this.setState({
                isLoading: false
            }, () => {
                confirmAlert({
                    title: 'CategoriesType',
                    message: response.message,
                    buttons: [
                        {
                            label: 'Ok',
                            onClick: () => {

                            }
                        }
                    ],
                    closeOnClickOutside: false,
                    closeOnEscape: false
                })
            })
        }
    }

    clearCategoriesType = () => {
        this.setState({
            selectedRow: null,
            isPopupOpen: false,
            organisationId: '',
            isDeleted: false,
        })
    }

    async onAddEditCategoriesType(type) {
        const { selectedRow } = this.state
        if (!selectedRow && type == 'edit') {
            return
        } else {
            if (type == 'edit') {
                this.setState({
                    isPopupOpen: true,
                    actionType: 'Update CategoriesType',
                    id: selectedRow.id,
                    typeName: selectedRow.typeName,
                    typeValue: selectedRow.typeValue,
                    typeDescription: selectedRow.typeDescription,
                    typeOrder: selectedRow.typeOrder,
                    typeDefault: selectedRow.typeDefault,
                    typeStatus: selectedRow.typeStatus
                })
            } else {
                this.setState({
                    isPopupOpen: true,
                    actionType: 'Add CategoriesType',
                    id: null,
                    typeName: '',
                    typeValue:'',
                    typeDescription:'',
                    typeOrder:'',
                    typeDefault:'',
                    typeStatus:'',
                })
            }
        }
    }

    async onDeleteCategoriesType() {
        const { selectedRow } = this.state
        confirmAlert({
            title: 'Delete CategoriesType',
            message: "Are you sure you wants to delete the record : " + selectedRow.vendorName + "?",
            buttons: [
                {
                    label: 'Confirm',
                    onClick: () => {
                        this.deleteCategoriesType()
                    }
                },
                {
                    label: 'Cancel',
                    onClick: () => {

                    }
                }
            ],
            closeOnCllickOutside: false,
            closeOnEscape: false
        })
    }

    async deleteCategoriesType() {
        const { selectedRow } = this.state
        this.setState({ isLoading: true })
        const r = await request
            .del(process.env.REACT_APP_API_URL + 'categoriestype/id/' + selectedRow.id)
            .set('authorization', `Bearer ${localStorage.getItem('admin_access_token')}`);
        const response = r.body
        if (response.success) {
            this.setState({
                id: null,
                typeName: '',
                typeValue:'',
                typeDescription:'',
                typeOrder:'',
                typeDefault:'',
                typeStatus:'',
                selectedRow: null
            }, () => {
                this.loadCategoriesType()
                confirmAlert({
                    title: 'CategoriesType',
                    message: response.message,
                    buttons: [
                        {
                            label: 'Ok',
                            onClick: () => {

                            }
                        }
                    ],
                    closeOnClickOutside: false,
                    closeOnEscape: false
                })
            })
        } else {
            this.setState({
                isLoading: false
            }, () => {
                confirmAlert({
                    title: 'CategoriesType',
                    message: response.message,
                    buttons: [
                        {
                            label: 'Ok',
                            onClick: () => {

                            }
                        }
                    ],
                    closeOnClickOutside: false,
                    closeOnEscape: false
                })
            })
        }
    }

    onTextChange = (e) => {
        this.setState({
            [e.target.id]: e.target.value
        })
    }

    handleImageUploadChange(event) {
        this.setState({
            gameBoard: URL.createObjectURL(event.target.files[0])
        })
    }

    onClickTab = (tabName) => {
        this.props.updateTabNavigation({ TabName: tabName })
    }

    handleSequenceNumber = selectedSequence => {
        this.setState(
            { selectedSequence },
            () => console.log(`Option selected:`, this.state.selectedSequence)
        );
    }

    onEditorCategoriesTypeChange = (editorCategoriesType) => {
        this.setState({
            editorCategoriesType,
            description: editorCategoriesType && draftToMarkdown(convertToRaw(editorCategoriesType.getCurrentContent())),
        })
    }

    onCountryChange = (newValue, actionMeta) => {
        if (actionMeta.action == "select-option") {
            this.setState({
                selectedCountry: newValue,
            })
        }
    }


    onSelectionChanged = () => {
        var selectedRows = this.gridApi.getSelectedRows();
        this.setState({
            selectedRow: selectedRows && selectedRows.length > 0 ? selectedRows[0] : null
        })
    }

    render() {
        const { isLoading } = this.state
        return (
            <LoadingOverlay
                active={isLoading}
                spinner
            >
                <div className="card m-10">
                    <section className="content">
                        <div className="row">
                            <div className="col-md-12">
                                <div className="card-body">
                                    <div style={{ padding: '0 15px 15px', display: 'inline-block', width: '100%' }}>
                                        <button
                                            disabled={!this.state.selectedRow}
                                            type="button"
                                            className="btn btn-danger float-right btn-sm"
                                            onClick={this.onDeleteCategoriesType}
                                        >{'Delete Record'}</button>
                                        <button
                                            disabled={!this.state.selectedRow}
                                            type="button"
                                            className="btn btn-success float-right btn-sm mx-2"
                                            onClick={this.onAddEditCategoriesType.bind(this, 'edit')}
                                        >{'Update Record'}</button>
                                        <button
                                            type="button"
                                            className="btn btn-success float-right btn-sm"
                                            onClick={this.onAddEditCategoriesType.bind(this, 'add')}
                                        >{'Add New'}</button>
                                    </div>
                                    <div className="card-body table-responsive p-0">
                                        <div style={{ width: '100%', height: '550px' }}>
                                            <div
                                                id="RoleNameGrid"
                                                style={{
                                                    height: '550px',
                                                    width: '100%',
                                                }}
                                                className="ag-theme-alpine"
                                            >
                                                <AgGridReact
                                                    modules={this.state.modules}
                                                    columnDefs={this.state.columnDefs}
                                                    defaultColDef={this.state.defaultColDef}
                                                    enableCellTextSelection={true}
                                                    rowSelection={this.state.rowSelection}
                                                    onGridReady={this.onGridReady}
                                                    rowData={this.state.rowData}
                                                    pagination={true}
                                                    paginationPageSize={this.state.paginationPageSize}
                                                    onSelectionChanged={this.onSelectionChanged.bind(this)}
                                                    rowDeselection={true}
                                                />
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </section>
                </div>
                <Popup open={this.state.isPopupOpen} closeOnDocumentClick={false} repositionOnResize={true}>
                    <section className="content">
                        <div className="row">
                            <div className="col-md-12">
                                <div className="form-group align-center">
                                    <h3 className="text-center m-2">{this.state.actionType} </h3>
                                </div>
                                <div style={{ marginTop: '40px' }}>
                                    <div className="card-body">
                                    <div className="form-group">
                                            <label htmlFor="typeName">Type Name </label>
                                            <input type="text" onChange={this.onTextChange} id="typeName" value={this.state.typeName} className="form-control" />
                                        </div>
                                        <div className="form-group">
                                            <label htmlFor="typeValue">Type Value </label>
                                            <input type="text" onChange={this.onTextChange} id="typeValue" value={this.state.typeValue} className="form-control" />
                                        </div>
                                        <div className="form-group">
                                            <label htmlFor="typeOrder">Type Order </label>
                                            <input type="text" onChange={this.onTextChange} id="typeOrder" value={this.state.typeOrder} className="form-control" />
                                        </div>
                                        <div className="form-group">
                                            <label htmlFor="typeDefault">Type Default </label>
                                            <input type="text" onChange={this.onTextChange} id="typeDefault" value={this.state.typeDefault} className="form-control" />
                                        </div>
                                        <div className="form-group">
                                            <label htmlFor="typeStatus">Type Status </label>
                                            <input type="text" onChange={this.onTextChange} id="typeStatus" value={this.state.typeStatus} className="form-control" />
                                        </div>
                                        <div className="form-group">
                                            <label htmlFor="typeDescription">Type Description </label>
                                            <textarea onChange={this.onTextChange} id="typeDescription" value={this.state.typeDescription} className="form-control" />
                                        </div>
                                    </div>
                                </div>
                                <div style={{ padding: '0 15px 15px', display: 'inline-block', width: '100%' }}>
                                    <button
                                        type="button"
                                        className="btn btn-warning float-right btn-sm mx-1"
                                        onClick={() => {
                                            this.setState({ isPopupOpen: false }, () => {
                                                this.clearCategoriesType()
                                            })
                                        }}
                                    >{'Cancel'}</button>
                                    <button
                                        type="button"
                                        className="btn btn-success float-right btn-sm mx-1"
                                        onClick={this.state.actionType == 'Update CategoriesType' ? this.onEditCategoriesType : this.onCreateCategoriesType}
                                    >{'Submit'}</button>
                                </div>
                            </div>
                        </div>
                    </section>
                </Popup>
            </LoadingOverlay>
        )
    }
}

const mapCategoriesTypeToProps = vendor => ({
    tabSettings: vendor.userReducer.tabSettings

})

const mapDispatchToProps = dispatch => ({
    updateTabNavigation: (data) => { dispatch(updateTabNavigation(data)) },
})

export default connect(
    mapCategoriesTypeToProps,
    mapDispatchToProps
)(CategoriesType)
