import React, { Component } from 'react';
import { connect } from 'react-redux'
import { updateTabNavigation } from '../store/actions'
import request from 'superagent';
import { confirmAlert } from 'react-confirm-alert'; // Import
import 'react-confirm-alert/src/react-confirm-alert.css' // Import css
import Select from 'react-select';
import LoadingOverlay from 'react-loading-overlay';
import { AllModules } from 'ag-grid-react';
import { AgGridReact } from 'ag-grid-react';
import Popup from "reactjs-popup";
import 'ag-grid-community/dist/styles/ag-grid.css';
import 'ag-grid-community/dist/styles/ag-theme-alpine.css';
import { Editor } from 'react-draft-wysiwyg';
import 'react-draft-wysiwyg/dist/react-draft-wysiwyg.css';
import { convertToRaw } from 'draft-js';
import draftToMarkdown from 'draftjs-to-markdown';
import CreatableSelect from 'react-select/creatable';

class RoleDetails extends Component {
    constructor(props) {
        super(props)
        this.state = {
            isLoading: false,
            roleDetailList: [],
            selectedSequence: null,
            modules: AllModules,
            columnDefs: [
                {
                    field: 'id',
                    headerName: 'RoleDetail Id',
                },
                {
                    field: 'entityName',
                    headerName: 'Entity Name',
                },
                {
                    field: 'entityRead',
                    headerName: 'Entity Read',
                },
                {
                    field: 'entityAdd',
                    headerName: 'Entity Add',
                },
                {
                    field: 'entityUpdate',
                    headerName: 'Entity Update',
                },
                {
                    field: 'entityPrint',
                    headerName: 'Entity Print',
                },
                {
                    field: 'entityExport',
                    headerName: 'Entity Export',
                },
                {
                    field: 'entityDelete',
                    headerName: 'Entity Delete',
                },
            ],
            defaultColDef: {
                flex: 1,
                minWidth: 100,
                sortable: true,
                resizable: true,
                autoHeight: true
            },
            getRowHeight: function (params) {
                return 18 * (Math.floor(params.data.description.length / 50) + 1);
            },
            rowSelection: 'single',
            selectedRow: null,
            paginationPageSize: 10,
            rowData: [],
            editorRoleDetail: null,
            userRoleList: [],
            selectedUserRole: null,
            recentlyCreatedOrgUserId: '',
            isPopupOpen: false,
            actionType: null,

            roleEntityId: null,
            entityRead: '',
            entityAdd: '',
            entityUpdate: '',
            entityPrint: '',
            entityExport: '',
            entityDelete: '',
            selectedRoleEntity: null,
            roleEntityList: []

        }
        this.onCreateRoleDetail = this.onCreateRoleDetail.bind(this)
        this.onAddEditRoleDetail = this.onAddEditRoleDetail.bind(this)
        this.onDeleteRoleDetail = this.onDeleteRoleDetail.bind(this)
        this.onEditRoleDetail = this.onEditRoleDetail.bind(this)

        process.env.REACT_APP_STAGE == 'production' ? this.apiBase = process.env.REACT_APP_API_URL + '/api' : this.apiBase = `${window.location.protocol}//${window.location.hostname}:${process.env.REACT_APP_API_PORT}`
    }

    onGridReady = params => {
        this.gridApi = params.api;
        this.gridColumnApi = params.columnApi;
    }

    componentDidMount() {
        this.loadRoleDetails()
        this.loadRoleEntity()
    }

    onBtStartEditing = (key, char, pinned) => {
        this.gridApi.setFocusedCell(0, 'lastName', pinned);
        this.gridApi.startEditingCell({
            rowIndex: 0,
            colKey: 'lastName',
            rowPinned: pinned,
            keyPress: key,
            charPress: char,
        });
    }

    async loadRoleDetails() {
        this.setState({
            isLoading: true
        })
        try {
            const r = await request
                .get(process.env.REACT_APP_API_URL + 'roledetails')
                .set('authorization', `Bearer ${localStorage.getItem('admin_access_token')}`);
            const response = r.body
            if (response.success) {
                this.setState({
                    rowData: response.ResponsePacket,
                    isLoading: false
                })
            } else {
                this.setState({
                    isLoading: false
                })
            }
        } catch (catchError) {
            this.setState({
                isLoading: false
            })
            alert("Error...")
        }
    }

    async loadRoleEntity() {
        this.setState({
            isLoading: true
        })
        try {
            const r = await request
                .get(process.env.REACT_APP_API_URL + 'roleentities')
                .set('authorization', `Bearer ${localStorage.getItem('admin_access_token')}`);
            const response = r.body
            if (response.success) {
                this.setState({
                    roleEntityList: response.ResponsePacket,
                    isLoading: false
                })
            } else {
                this.setState({
                    isLoading: false
                })
            }
        } catch (catchError) {
            this.setState({
                isLoading: false
            })
            alert("Error...")
        }
    }

    async onCreateRoleDetail() {
        const {
            roleEntityId,
            entityRead,
            entityAdd,
            entityUpdate,
            entityPrint,
            entityExport,
            entityDelete,


        } = this.state
        this.setState({
            isLoading: true
        })

        const r = await request
            .post(process.env.REACT_APP_API_URL + 'roledetails')
            .send({
                roleEntityId,
                entityRead,
                entityAdd,
                entityUpdate,
                entityPrint,
                entityExport,
                entityDelete,
            })
        const response = r.body

        if (response.success) {
            this.setState({
                roleEntityId: null,
                entityRead: '',
                entityAdd: '',
                entityUpdate: '',
                entityPrint: '',
                entityExport: '',
                entityDelete: '',

                isLoading: false,
                isPopupOpen: false
            }, () => {
                this.loadRoleDetails()
                confirmAlert({
                    title: 'RoleDetails',
                    message: response.message,
                    buttons: [
                        {
                            label: 'Ok',
                            onClick: () => {

                            }
                        }
                    ],
                    closeOnClickOutside: false,
                    closeOnEscape: false
                })
            })
        } else {
            this.setState({
                isLoading: false
            }, () => {
                confirmAlert({
                    title: 'RoleDetails',
                    message: response.message,
                    buttons: [
                        {
                            label: 'Ok',
                            onClick: () => {

                            }
                        }
                    ],
                    closeOnClickOutside: false,
                    closeOnEscape: false
                })
            })
        }
    }

    async onEditRoleDetail() {
        const {
            selectedRow,
            id,
            roleEntityId,
            entityRead,
            entityAdd,
            entityUpdate,
            entityPrint,
            entityExport,
            entityDelete,

        } = this.state
        this.setState({
            isLoading: true
        })

        const r = await request
            .patch(process.env.REACT_APP_API_URL + 'roledetails/id/' + selectedRow.id)
            .set('authorization', `Bearer ${localStorage.getItem('admin_access_token')}`)
            .send({
                id,
                roleEntityId,
                entityRead,
                entityAdd,
                entityUpdate,
                entityPrint,
                entityExport,
                entityDelete,

            })
        const response = r.body
        if (response.success) {
            this.setState({
                isLoading: false,
                isPopupOpen: false,
                selectedRow: null
            }, () => {
                this.loadRoleDetails()
                confirmAlert({
                    title: 'RoleDetails',
                    message: response.message,
                    buttons: [
                        {
                            label: 'Ok',
                            onClick: () => {

                            }
                        }
                    ],
                    closeOnClickOutside: false,
                    closeOnEscape: false
                })
            })
        } else {
            this.setState({
                isLoading: false
            }, () => {
                confirmAlert({
                    title: 'RoleDetails',
                    message: response.message,
                    buttons: [
                        {
                            label: 'Ok',
                            onClick: () => {

                            }
                        }
                    ],
                    closeOnClickOutside: false,
                    closeOnEscape: false
                })
            })
        }
    }

    clearRoleDetail = () => {
        this.setState({
            selectedRow: null,
            isPopupOpen: false,
            organisationId: '',
            isDeleted: false,
        })
    }

    async onAddEditRoleDetail(type) {
        const { selectedRow, roleEntityList } = this.state
        if (!selectedRow && type == 'edit') {
            return
        } else {
            if (type == 'edit') {
                this.setState({
                    isPopupOpen: true,
                    actionType: 'Update RoleDetail',
                    id: selectedRow.id,
                    roleEntityId: selectedRow.roleEntityId,
                    entityRead: selectedRow.entityRead,
                    entityAdd: selectedRow.entityAdd,
                    entityUpdate: selectedRow.entityUpdate,
                    entityPrint: selectedRow.entityPrint,
                    entityExport: selectedRow.entityExport,
                    entityDelete: selectedRow.entityDelete,
                    selectedRoleEntity: { id: selectedRow.roleEntityId, entityName: roleEntityList.find(x => x.id === selectedRow.roleEntityId).entityName }

                })
            } else {
                this.setState({
                    isPopupOpen: true,
                    actionType: 'Add RoleDetail',
                    id: null,
                    roleEntityId: null,
                    entityRead: '',
                    entityAdd: '',
                    entityUpdate: '',
                    entityPrint: '',
                    entityExport: '',
                    entityDelete: '',

                })
            }
        }
    }

    async onDeleteRoleDetail() {
        const { selectedRow } = this.state
        confirmAlert({
            title: 'Delete RoleDetail',
            message: "Are you sure you wants to delete the record : " + selectedRow.id + "?",
            buttons: [
                {
                    label: 'Confirm',
                    onClick: () => {
                        this.deleteRoleDetail()
                    }
                },
                {
                    label: 'Cancel',
                    onClick: () => {

                    }
                }
            ],
            closeOnCllickOutside: false,
            closeOnEscape: false
        })
    }

    async deleteRoleDetail() {
        const { selectedRow } = this.state
        this.setState({ isLoading: true })
        const r = await request
            .del(process.env.REACT_APP_API_URL + 'roledetails/id/' + selectedRow.id)
            .set('authorization', `Bearer ${localStorage.getItem('admin_access_token')}`);
        const response = r.body
        if (response.success) {
            this.setState({
                id: null,
                roleEntityId: null,
                entityRead: '',
                entityAdd: '',
                entityUpdate: '',
                entityPrint: '',
                entityExport: '',
                entityDelete: '',
                selectedRow: null
            }, () => {
                this.loadRoleDetails()
                confirmAlert({
                    title: 'RoleDetail',
                    message: response.message,
                    buttons: [
                        {
                            label: 'Ok',
                            onClick: () => {

                            }
                        }
                    ],
                    closeOnClickOutside: false,
                    closeOnEscape: false
                })
            })
        } else {
            this.setState({
                isLoading: false
            }, () => {
                confirmAlert({
                    title: 'RoleDetail',
                    message: response.message,
                    buttons: [
                        {
                            label: 'Ok',
                            onClick: () => {

                            }
                        }
                    ],
                    closeOnClickOutside: false,
                    closeOnEscape: false
                })
            })
        }
    }

    onTextChange = (e) => {
        this.setState({
            [e.target.id]: e.target.value
        })
    }

    handleImageUploadChange(event) {
        this.setState({
            gameBoard: URL.createObjectURL(event.target.files[0])
        })
    }

    onClickTab = (tabName) => {
        this.props.updateTabNavigation({ TabName: tabName })
    }

    onChangeRoleEntity = (newValue, actionMeta) => {
        if (actionMeta.action == "select-option") {
            this.setState({
                selectedRoleEntity: newValue,
                roleEntityId: newValue.id
            })
        }
    }

    onEditorRoleDetailChange = (editorRoleDetail) => {
        this.setState({
            editorRoleDetail,
            description: editorRoleDetail && draftToMarkdown(convertToRaw(editorRoleDetail.getCurrentContent())),
        })
    }

    onSelectionChanged = () => {
        var selectedRows = this.gridApi.getSelectedRows();
        this.setState({
            selectedRow: selectedRows && selectedRows.length > 0 ? selectedRows[0] : null
        })
    }

    render() {
        const { roleDetailList, isLoading } = this.state
        return (
            <LoadingOverlay
                active={isLoading}
                spinner
            >
                <div className="card m-10">
                    <section className="content">
                        <div className="row">
                            <div className="col-md-12">
                                <div className="card-body">
                                    <div style={{ padding: '0 15px 15px', display: 'inline-block', width: '100%' }}>
                                        <button
                                            disabled={!this.state.selectedRow}
                                            type="button"
                                            className="btn btn-danger float-right btn-sm"
                                            onClick={this.onDeleteRoleDetail}
                                        >{'Delete Record'}</button>
                                        <button
                                            disabled={!this.state.selectedRow}
                                            type="button"
                                            className="btn btn-success float-right btn-sm mx-2"
                                            onClick={this.onAddEditRoleDetail.bind(this, 'edit')}
                                        >{'Update Record'}</button>
                                        <button
                                            type="button"
                                            className="btn btn-success float-right btn-sm"
                                            onClick={this.onAddEditRoleDetail.bind(this, 'add')}
                                        >{'Add New'}</button>
                                    </div>
                                    <div className="card-body table-responsive p-0">
                                        <div style={{ width: '100%', height: '550px' }}>
                                            <div
                                                id="RoleNameGrid"
                                                style={{
                                                    height: '550px',
                                                    width: '100%',
                                                }}
                                                className="ag-theme-alpine"
                                            >
                                                <AgGridReact
                                                    modules={this.state.modules}
                                                    columnDefs={this.state.columnDefs}
                                                    defaultColDef={this.state.defaultColDef}
                                                    enableCellTextSelection={true}
                                                    rowSelection={this.state.rowSelection}
                                                    onGridReady={this.onGridReady}
                                                    rowData={this.state.rowData}
                                                    pagination={true}
                                                    paginationPageSize={this.state.paginationPageSize}
                                                    onSelectionChanged={this.onSelectionChanged.bind(this)}
                                                    rowDeselection={true}
                                                />
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </section>
                </div>
                <Popup open={this.state.isPopupOpen} closeOnDocumentClick={false} repositionOnResize={true}>
                    <section className="content">
                        <div className="row">
                            <div className="col-md-12">
                                <div className="form-group align-center">
                                    <h3 className="text-center m-2">{this.state.actionType} </h3>
                                </div>
                                <div style={{ marginTop: '40px' }}>
                                    <div className="card-body">
                                        <div className="form-group">
                                            <label htmlFor="roleEntityId">RoleEntity Id </label>
                                            <Select
                                                // className="form-control"
                                                id={"org"}
                                                value={this.state.selectedRoleEntity}
                                                onChange={this.onChangeRoleEntity}
                                                options={this.state.roleEntityList}
                                                getOptionLabel={(option) => option.entityName}
                                                getOptionValue={(option) => option.id}
                                            />
                                        </div>
                                        <div className="form-group">
                                            <label htmlFor="entityRead">Entity Read </label>
                                            <input type="text" onChange={this.onTextChange} id="entityRead" value={this.state.entityRead} className="form-control" />
                                        </div>
                                        <div className="form-group">
                                            <label htmlFor="entityAdd">EntityAdd </label>
                                            <input type="text" onChange={this.onTextChange} id="entityAdd" value={this.state.entityAdd} className="form-control" />
                                        </div>
                                        <div className="form-group">
                                            <label htmlFor="entityUpdate">EntityUpdate </label>
                                            <input type="text" onChange={this.onTextChange} id="entityUpdate" value={this.state.entityUpdate} className="form-control" />
                                        </div>
                                        <div className="form-group">
                                            <label htmlFor="entityPrint">Entity Print </label>
                                            <input type="text" onChange={this.onTextChange} id="entityPrint" value={this.state.entityPrint} className="form-control" />
                                        </div>
                                        <div className="form-group">
                                            <label htmlFor="entityExport">Entity Export </label>
                                            <input type="text" onChange={this.onTextChange} id="entityExport" value={this.state.entityExport} className="form-control" />
                                        </div>
                                        <div className="form-group">
                                            <label htmlFor="entityDelete">Entity Delete </label>
                                            <input type="text" onChange={this.onTextChange} id="entityDelete" value={this.state.entityDelete} className="form-control" />
                                        </div>
                                    </div>
                                </div>
                                <div style={{ padding: '0 15px 15px', display: 'inline-block', width: '100%' }}>
                                    <button
                                        type="button"
                                        className="btn btn-warning float-right btn-sm mx-1"
                                        onClick={() => {
                                            this.setState({ isPopupOpen: false }, () => {
                                                this.clearRoleDetail()
                                            })
                                        }}
                                    >{'Cancel'}</button>
                                    <button
                                        type="button"
                                        className="btn btn-success float-right btn-sm mx-1"
                                        onClick={this.state.actionType == 'Update RoleDetail' ? this.onEditRoleDetail : this.onCreateRoleDetail}
                                    >{'Submit'}</button>
                                </div>
                            </div>
                        </div>
                    </section>
                </Popup>
            </LoadingOverlay>
        )
    }
}

const mapRoleDetailToProps = roleDetail => ({
    tabSettings: roleDetail.userReducer.tabSettings,
    roleDetailList: roleDetail.userReducer.playerList,

})

const mapDispatchToProps = dispatch => ({
    updateTabNavigation: (data) => { dispatch(updateTabNavigation(data)) },
})

export default connect(
    mapRoleDetailToProps,
    mapDispatchToProps
)(RoleDetails)
